.App {
  text-align: center;
}
input {
  padding: 4px;
}
.input-struc {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 20px;
}
.input-struc label {
  margin-left: 10px;
}
.forDOB {
  display: flex;
  border: 1px solid gray;
  border-radius: 6px;
  position: relative;
  width: 700px;
  margin: 10px;
  margin-left: 0px;
  padding: 5px;
  line-height: 6ex;
}

.forDOB label {
  position: absolute;
  top: -3ex;
  z-index: 1;
  left: 1em;
  background-color: #dcdcdc;
  padding: 0 5px;
}
.forDOB input {
  width: 250px;
  height: 35px;
  border-radius: 10px;
}
.name-input input {
  width: 700px;
  height: 40px;
  border-radius: 10px;
  font-size: 15px;
  border-color: grey;
}
.sbmt-btn {
  background-color: yellow;
  border-radius: 15px;
  height: 50px;
  width: 150px;
  font-size: large;
  cursor: pointer;
  margin-top: 20px;
}
